.pie {
    position: relative;
    display: inline-block;
    background-image: conic-gradient(#339de5 calc(3.6deg * var(--percent)),rgba(0,0,0,0) calc(3.6deg * var(--percent)));
    background-blend-mode: normal;
    background-position: 50% 50%;
    background-size: 150%;
    width: 5em;
    height: 5em;
    border-radius: 50%;
}

/* show the percentage (thanks to Ana Tudor for the counter() trick) */
.pie--value::after {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    counter-reset: amount var(--amount);
    content: "$"counter(amount);
    color: #fff;
    text-shadow: 1px 1px 1px #262626;
}
.pie--disc::before {
    content: '';
    position: absolute;
    top: 0.5em;
    left: 0.5em;
    right: 0.5em;
    bottom: 0.5em;
    border-radius: 50%;
    background: #fff;
}
.pie--disc::after {
    color: #262626;
    text-shadow: none;
}

